import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';

const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
    border: `1px solid ${theme.palette.divider}`,
    '&:not(:last-child)': {
        borderBottom: 0,
    },
    '&:before': {
        display: 'none',
    },
}));

const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
        expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
        {...props}
    />
))(({ theme }) => ({
    backgroundColor:
        theme.palette.mode === 'dark'
            ? 'rgba(255, 255, 255, .05)'
            : 'rgba(0, 0, 0, .03)',
    flexDirection: 'row-reverse',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
        transform: 'rotate(90deg)',
    },
    '& .MuiAccordionSummary-content': {
        marginLeft: theme.spacing(1),
    },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(2),
    borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export default function CustomizedAccordions() {
    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <div>
            <h1>Ralisations</h1>
            <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                    <Typography><span>Petit Foetus</span></Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Dmo ralise en Symfony pour une artiste tatoueuse. L'application dispose d'un back-office pour que l'utilisateur puisse grer ses images et ses donnes (le back-office n'est pas accessible publiquement).

                        <br />
                        <br />
                        Le site dispose aussi d'un convertisseur d'images afin de contrler, rduire et optimiser la taille des fichiers envoys.
                        <br />
                        <br />
                        <a href="https://pydonze.fr/foetus/public/home" target='_blank'>Lien vers le site</a>
                        <br />
                        <a href="https://github.com/pierreyvesdonze/foetus" target='_blank'>Lien vers le dpt Github</a>
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
                    <Typography><span>MyFood</span></Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Ralis avec Symfony et Javascript ce site permet  l'utilisateur enregistr de crer ses propres recettes de cuisine, de crer des liste de courses et de rechercher des recettes.
                        <br />
                        Plusieurs fonctions de recherche ont t implmentes, dont une fonction de recherche par ingrdient pour que l'utilisateur puisse trouver des recettes en fonction de ce dont il dispose (dans cette dmo les ingrdients sont trs limits).
                        <br />
                        <br />
                        <a href='https://pydonze.fr/myfood/public/' target='_blank'>Visiter le site</a>
                        <br />
                        <a href='https://github.com/pierreyvesdonze/myfood' target='_blank'>Visiter le dpt Github</a>
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
                    <Typography><span>Catshop</span></Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Catshop est un faux site e-commerce ralis avec Symfony. Le but tait un entranement  enregistrer des donnes en session, les retrouver aprs login mais aussi de crer un tunnel d'achat (jusqu' un faux procd de paiement).
                        <br />
                        <br />
                        <a href='https://pydonze.fr/catshop/public/' target='_blank'>Visiter le site</a>
                        <br />
                        <a href='https://github.com/pierreyvesdonze/catshop' target='_blank'>Visiter le dpt Github</a>
                    </Typography>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
                    <Typography><span>Ce portfolio</span></Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Ce portfolio est entirement ralis avec React et Material-UI.
                        <br />
                        <br />
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
